.about-us {
  position: relative;
  padding: 120px 0 80px;
  background: #353535;
  overflow: hidden;
}
.about-us__before {
  position: absolute;
  height: 40px;
  width: 48%;
  background: #fff;
  top: 0;
  right: 0;
}
.about-us__before::before {
  content: "";
  left: -100px;
  position: absolute;
  border: 100px solid transparent;
  border-top: 40px solid #fff;
  border-right: 0px solid #fff;
}
.about-us__after {
  position: absolute;
  height: 40px;
  width: 48%;
  background: #fff;
  bottom: 0;
  left: 0;
}
.about-us__after::after {
  content: "";
  position: absolute;
  right: -245px;
  border: 70px solid transparent;
  border-bottom: 0px solid #fff;
  border-left: 175px solid #fff;
}
.about-us .container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: unset;
  flex-direction: row;
  gap: 30px;
}
.about-us__left {
  max-width: 500px;
}
.about-us__left .title {
  color: #fff;
  margin-bottom: 32px;
  text-align: left;
}
.about-us__left .text {
  color: #fff;
}
.about-us__right {
  position: relative;
  max-width: 545px;
  max-height: 300px;
}
.about-us #myVideo {
  width: 100%;
  height: 100%;
}
.about-us__play, .about-us__pause {
  width: 80px;
  height: 80px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #353535;
  opacity: 0.25;
  border-radius: 50%;
}
.about-us__play .text, .about-us__pause .text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 1;
  color: #d9d9d9;
}
.about-us__pause {
  display: none;
}

