@import "./nullstyle";
@import "./variable";
@import "./fonts";
@import "./mixins";

html {
  scroll-behavior: smooth;
}

.container {
  max-width: 1140px;
  padding: 0 10px;
  margin: 0 auto;
}

.title {
  font-family: $font-family-title;
  font-size: $title-fz;
  text-transform: uppercase;
  text-align: center;
  color: $text-color;
}

.sub-title {
  font-family: $font-family-title;
  font-size: $sub-title-fz;
  line-height: 150%;
  text-transform: uppercase;
  color: $text-color;
}

.text {
  font-family: $font-family-text;
  font-size: $text-fz;
  line-height: 150%;
  color: $text-color;
}

.opacity {
  right: -100%;
  background: $black-color;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  min-width: 100%;
  min-height: 100%;
  @include transition;
  &_active {
    opacity: 0.7;
  }
}

.relative {
  position: relative;
}
.swiper-button-prev,
.swiper-button-next {
  width: 45px !important;
  height: 45px !important;
  background: white;
  border-radius: 999px;
  border: 1px solid $text-color;
}
.swiper-button-prev {
  transform: translateX(-50%);
  position: absolute !important;
  //   left = container padding
  left: 20px !important;
  &::after {
    content: "";
    display: none;
  }
}
.swiper-button-next {
  transform: translateX(50%);
  position: absolute;
  right: 20px !important;
  &::after {
    content: "";
    display: none;
  }
}

// HEADER

.header {
  padding: 14px 0;
  background: $contrast-color;
  border-bottom: 1px solid $black-color;
  &__wrapper {
    @include flex(space-between);
    max-width: 1140px;
    padding: 0 10px;
    margin: 0 auto;
  }
  &__right {
    @include flex(space-between);
    max-width: 950px;
    width: 100%;
    @include transition;
  }
  &__menu {
    display: flex;
    gap: 24px;
  }
  &__link {
    font-family: $font-family-text;
    font-size: $link-fz;
    font-weight: 500;
    color: $black-color;
    padding-bottom: 3px;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 2px;
      background: $black-color;
      transform: scaleX(0);
      transform-origin: right;
      transition: transform 0.5s;
    }
    &:hover {
      &::after {
        transform: scaleX(1);
        transform-origin: left;
      }
    }
  }
  &__info {
    display: flex;
    align-items: center;
    gap: 60px;
  }
  &__lang {
    display: flex;
    align-items: center;
    gap: 4px;
  }
  .current {
    opacity: 0.5;
    &::after {
      content: unset;
    }
  }
  &__stick {
    padding-bottom: 5px;
  }
  &__button {
    font-family: $font-family-text;
    font-size: $link-fz;
    font-weight: 500;
    color: $primary-color;
    background: $contrast-color;
    padding: 16px 36px;
    border: 1px solid $primary-color;
    @include transition;
    &:hover {
      background: $primary-color;
      color: $contrast-color;
    }
  }
  .burger-btn {
    display: none;
    z-index: 10;
    position: relative;
    width: 30px;
    height: 20px;
    cursor: pointer;
    > span {
      position: absolute;
      background-color: $black-color;
      width: 100%;
      height: 3px;
      border-radius: 18px;
      left: 0;
      top: 9px;
      @include transition;
    }
    &:after,
    &:before {
      content: "";
      position: absolute;
      background-color: $black-color;
      width: 100%;
      height: 3px;
      border-radius: 18px;
      left: 0;
      @include transition;
    }
    &:before {
      top: 20px;
    }
    &:after {
      bottom: 20px;
    }
    &_active {
      span {
        opacity: 0;
        background-color: $primary-color;
      }
      &:before {
        transform: rotate(45deg);
        top: 9px;
        background-color: $primary-color;
      }
      &:after {
        transform: rotate(-45deg);
        bottom: 9px;
        background-color: $primary-color;
      }
    }
  }
  @media screen and (max-width: 920px) {
    .burger-btn {
      display: block;
    }
    &__right {
      max-width: 320px;
      width: 100%;
      flex-direction: column;
      background: $black-color;
      padding: 100px 20px 50px 20px;
      position: absolute;
      transform-origin: right;
      transform: scaleX(0);
      top: 0;
      right: -100%;
      gap: 30px;
      &_active {
        transform: scaleX(1);
        right: 0;
      }
    }
    &__menu {
      flex-direction: column;
      width: 100%;
    }
    &__link {
      color: $contrast-color;
      &::after {
        background-color: $contrast-color;
      }
    }
    &__info {
      gap: unset;
      justify-content: space-between;
      width: 100%;
    }
    &__stick {
      color: $contrast-color;
    }
    &__button {
      background: $primary-color;
      color: $contrast-color;
    }
  }
}

.main-screen {
  width: 100%;
  // max-height: 600px;
}

// POINTS

.points {
  padding: 165px 0 145px 0;
  .container {
    @include flex(space-evenly, $flex-wrap: wrap);
    gap: 30px;
  }
  &__card {
    max-width: 255px;
  }
  &__img {
    margin-bottom: 27px;
  }
  .sub-title {
    margin-bottom: 8px;
    color: $text-color;
  }
  .text {
    color: $text-color;
  }
  @media screen and (max-width: 768px) {
    padding: 65px 0 45px 0;
    gap: 20px;
  }
  @media screen and (max-width: 549px) {
    &__card {
      text-align: center;
    }
  }
}

// ABOUT-US

.about-us {
  position: relative;
  padding: 120px 0 80px;
  background: $text-color;
  overflow: hidden;
  &__before {
    position: absolute;
    height: 40px;
    width: 48%;
    background: $contrast-color;
    top: 0;
    right: 0;
    &::before {
      content: "";
      left: -100px;
      position: absolute;
      border: 100px solid transparent;
      border-top: 40px solid #fff;
      border-right: 0px solid #fff;
    }
  }
  &__after {
    position: absolute;
    height: 40px;
    width: 48%;
    background: $contrast-color;
    bottom: 0;
    left: 0;
    &::after {
      content: "";
      position: absolute;
      right: -245px;
      border: 70px solid transparent;
      border-bottom: 0px solid #fff;
      border-left: 175px solid #fff;
    }
  }
  .container {
    @include flex(space-evenly, $flex-wrap: wrap, $align-items: unset);
    gap: 30px;
  }
  &__left {
    max-width: 500px;
    .title {
      color: $contrast-color;
      margin-bottom: 32px;
      text-align: left;
    }
    .text {
      color: $contrast-color;
    }
  }
  &__right {
    position: relative;
    max-width: 545px;
    max-height: 300px;
  }
  #myVideo {
    width: 100%;
    height: 100%;
  }
  &__play,
  &__pause {
    width: 80px;
    height: 80px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: $text-color;
    opacity: 0.25;
    border-radius: 50%;
    .text {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      opacity: 1;
      color: #d9d9d9;
    }
  }
  &__pause {
    display: none;
  }
}

// PROJECTS

.projects {
  padding: 160px 0;
  .title {
    text-align: center;
    color: $text-color;
    margin-bottom: 60px;
  }
  &__categories {
    @include flex(space-between);
    margin-bottom: 24px;
  }
  &__tab {
    flex-basis: 25%;
    font-family: $font-family-title;
    font-size: $tab-fz;
    color: $text-color-extra-light;
    padding-bottom: 16px;
    text-align: center;
    border-bottom: 1px solid $text-color-extra-light;
    @include transition;
    cursor: pointer;
    &:hover,
    &_active {
      color: $primary-color;
      border-color: $primary-color;
    }
  }
  &__content {
    width: 100%;
    height: 200px;
  }
  #tab-content-1 {
    background: red;
  }
  #tab-content-2 {
    background: blue;
  }
  #tab-content-3 {
    background: green;
  }
  #tab-content-4 {
    background: purple;
  }
  .hide {
    display: none;
  }
  .show {
    display: block;
  }
  @media screen and (max-width: 768px) {
    padding: 60px 0;
    &__tab {
      font-size: 12px;
    }
  }
}

// news
.news {
  padding: 160px 0 80px 0;
  .title {
    margin-bottom: 60px;
  }
}
.news-slider {
  &__wrapper {
    max-width: 350px;
  }
  &__img {
    width: 350px;
    height: 350px;
    margin: 0 auto;
    margin-bottom: 20px;
  }
  .section-title {
    margin-bottom: 10px;
  }
  .text {
    margin-bottom: 10px;
  }
  &__date {
    margin-bottom: 10px;
    font-size: 16px;
    font-family: $font-family-text;
    color: $text-color-extra-light;
  }
  @media screen and (max-width: 768px) {
    padding: 60px 0 20px 0;
  }
}
// partners

.partners {
  padding: 80px 0 160px 0;
  .title {
    margin-bottom: 60px;
  }
  .swiper-button-next {
    transform: translate(90%);
  }
  .swiper-button-prev {
    transform: translate(-90%);
  }
  @media screen and (max-width: 768px) {
    padding: 20px 0 80px 0;
  }
}
.partners-slider {
  &__slide {
    // padding-left: 2px;
    img {
      width: 250px;
      height: 250px;
      border: 1px solid $text-color-extra-light;
      //   margin-right: 1px;
    }
  }
}

// founders

.founders {
  background: $primary-color-black center center no-repeat;
  //   background-size: 100% fill;
  //   background-size: 100%;

  //   height: 930px;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    border: 70px solid transparent;
    border-top: 70px solid $contrast-color;
    border-right: 70px solid $contrast-color;
  }
  .title {
    margin-bottom: 60px;
    color: $contrast-color;
  }
  .sub-title {
    margin-bottom: 8px;
    color: $text-color;
  }
  &__after {
    position: absolute;
    z-index: 1;
    bottom: 0;
    left: 0;
    width: 92%;
    height: 150px;
    background: $contrast-color;
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      right: -150px;
      border: 75px solid transparent;
      border-bottom: 75px solid #fff;
      border-left: 75px solid #fff;
    }
  }
  &__item {
    max-width: 448px;
    z-index: 3;
    &:first-child {
      margin-right: 30px;
    }
  }
  &__wrapper {
    padding-top: 60px;
  }
  &__flex {
    @include flex;
  }
  &__img {
    height: 600px;
    margin-bottom: 13px;
  }
  .section-title {
    margin-bottom: 10px;
  }
}

// reviews

.reviews {
  padding: 160px 0 110px 0;
  .slide-prev,
  .slide-next {
    @include flex;
    width: 45px;
    height: 45px;
    border: 1px solid $text-color;
    border-radius: 999px;
  }
  @media screen and (max-width: 768px) {
    padding: 60px 0 40px 0;
  }
}
.reviews-slider {
  .swiper-wrapper {
    padding: 50px 0;
  }
  &__slides {
    padding: 0 45px 25px;
    background: $text-color;
  }
  &__img {
    border: 2px white solid;
    border-radius: 999px;
    transform: translateY(-50%);
  }
  &__text {
    color: white;
    font-family: $font-family-text;
    margin-bottom: 20px;
    line-height: 150%;
  }

  &__name {
    color: white;
    font-family: $font-family-text;
    text-align: center;
  }
}
.flex-center {
  @include flex;
}
.flex-end {
  @include flex(flex-end);
  .slide-prev {
    margin-right: 20px;
  }
}

// contact

.contact {
  padding: 110px 0 175px 0;
  > .text {
    text-align: center;
    margin-bottom: 60px;
  }
  .sub-title {
    text-align: center;
    color: $contrast-color;
    margin-bottom: 16px;
  }
  &__map {
    background: url("../img/Basemap\ image.png");
    height: 500px;
    width: 100%;
    @include flex(flex-start);
    padding-left: 90px;

    .section-title {
      text-align: center;
      margin-bottom: 15px;
      color: white;
    }
    .text {
      //   margin-bottom: 15px;
      color: white;
    }
  }
  &__info {
    padding: 55px 70px;
    height: 530px;
    width: 350px;
    background: $primary-color-black;
    @include flex(space-between, $flex-direction: column);
  }
  @media screen and (max-width: 768px) {
    padding: 40px 0 60px 0;
  }
}
.mb-15 {
  margin-bottom: 15px;
}
.mr-small {
  margin-right: 10px;
}
.social {
  @include flex;
  width: 40px;
  height: 40px;
  background: white;
  border-radius: 999px;
}
.text-center {
  text-align: center;
}
.w-full {
